
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import comDetailHeader from '../../components/header/headDetailNavBar.vue'
import comDetailFooter from '../../components/footer/footDetailNavBar.vue'
import { mapGetters, mapActions } from 'vuex'
export default {
name: 'musicDetail',
components: {
Toast,
XButton,
'com-detail-header': comDetailHeader,
'com-detail-footer': comDetailFooter
},
computed: {
...mapGetters('storeMusicPage', [
'article',
'author',
'storyAuthor',
'musicCover',
'musicUrl'
]),
headerTitle: function () {
return this.tag ? this.tag : '一个音乐'
},
backgroundUrl: function () {
return 'url(' + this.musicCover + ')'
}
},
created () {
console.log(this.$route.params)
window.scrollTo(0, 0)
this.getMusicById(this.$route.params.id)
},
data: function () {
return {
toastStr: '很抱歉，此歌曲已经下架，无法播放',
showToast: false,
showPlay: true
}
},
methods: {
...mapActions('storeMusicPage', [
'getMusicById',
'getMusicUrlById'
]),
musicPlayerControlClick: function () {
if (this.musicUrl === '') {
this.showToast = true
return
}
if (this.showPlay) {
this.showPlay = false
document.getElementById('musicPlayer').play()
} else {
this.showPlay = true
document.getElementById('musicPlayer').pause()
}
}
}
}



